html{box-sizing:border-box;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}body{margin:0}*,*::before,*::after{box-sizing:inherit}ul,li,h1,h2,h3,p,button{padding:0;margin:0}ul{list-style:none}.card-rooms{width:70%;margin-left:15%;margin-right:15%}.room-item{margin-bottom:5%}.room-item-header{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;background-color:#ffea00}.book-item-header{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;background-color:#2962ff;color:white}.filter-area{display:-webkit-flex;display:flex;width:100%}.filter-select{display:block;width:100%;margin-left:10px;padding-left:15px}.cursor-pointer{cursor:pointer}.button-link{background:none !important;border:none;padding:0 !important;text-decoration:underline;font-weight:500 !important}.btn-book{border-radius:10px}.container-loader{position:fixed;width:100%;height:100%;top:0;left:0;z-index:1000;background:#f8f8f8ad}.container-loader .img-loader{top:30%;left:48%;z-index:1000;position:absolute}.card-detail-screen{width:100%}

